import React, { useState } from "react";
import Card from "../UI/Card";
import ExpensesFilter from "./ExpensesFilter";
import ExpensesList from "./ExpensesList";
import ExpensesChart from "./ExpensesChart";

import './Expenses.css';
const Expenses = (props) => {
    const [year,setYear] = useState('2023')

    const yearHandler = (selectedYear)=>{
        setYear(selectedYear)
    }
    const filteredExpenses = props.items.filter(filteredExp=>{
        return filteredExp.date.getFullYear().toString()===year
    })
    // let filteredContent = <p>NO Any Expenses</p>;
    // if (filteredExpenses.length>0){
    //     filteredContent = filteredExpenses.map(expense=>
    //         <ExpenseItem 
    //             key = {expense.id}
    //             title = {expense.title} 
    //             amount ={expense.amount} 
    //             date = {expense.date}
    //         />)
    // }

    return(
        <div>
        <Card className="expenses">
            <ExpensesChart expenses = {filteredExpenses}/>
            <ExpensesFilter selected = {year} onSave = {yearHandler} />
            <ExpensesList filteredExpenses = {filteredExpenses} />
        </Card>
        </div>
    )
    
    

}
export default Expenses;